.team {
    flex: 0 0 79%;
    p {
        &:first-of-type {
            margin-bottom: 15px; }
        &:last-of-type {
            margin-bottom: 16px; } }

    &__name {
        font-family: 'Raleway', sans-serif;
        font-style: normal;
        font-weight: 800;
        font-size: 24px;
        line-height: 28px;
        color: #000000; }
    &__position {
        font-family: 'Roboto', sans-serif;
        font-style: normal;
        font-weight: 300;
        font-size: 12px;
        line-height: 200%;
        letter-spacing: 2px;
        color: #0083CA; }
    &__more {
        font-family: 'Roboto', sans-serif;
